    font-family: 'radio-canada-big';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format('truetype');
}

:root {
    --lower-layer-background-color: #82D0D9;
    --middle-layer-background-color: #36B1BF;
    --upper-layer-background-color: #037F8C;
    --dark-font-color: #F272A1;
    --light-font-color: #F299B1;
    --icon-filter-dark: invert(64%) sepia(37%) saturate(2182%) hue-rotate(298deg) brightness(99%) contrast(91%);

    --font-family: radio-canada-big, Helvetica, Arial, sans-serif;
    --main-font-size: 30px;
    --main-font-weight: 600;
    --main-letter-spacing: 2px;
    --normal-font-size: 20px;
    --normal-font-weight: 400;
    --normal-letter-spacing: 1px;
}

body {
    background-color: var(--lower-layer-background-color);
    font-family: var(--font-family);
    text-align: center;
    font-family: var(--font-family);
    font-size: var(--normal-font-size);
    font-weight: var(--normal-font-weight);
    letter-spacing: var(--normal-letter-spacing);
    color: var(--light-font-color);
}

::placeholder {
    color: var(--dark-font-color);
    opacity: 0.5;
}

#search-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: conter;
    position: relative;
}

#content-container {
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
}

#search-container, 
#result-container,
.error-container {
    background-color: var(--middle-layer-background-color);
    width: 600px;
    padding: 10px 20px;
    border-radius: 10px;
}

#search-container {
    display: flex;
    gap: 20px;
    align-items: center;
    justify-content: center;
}

#search-btn {
    background-color: var(--middle-layer-background-color);
    border: 0;
}

#search-input {
    width: 500px;
    border: 0;
    background-color: var(--middle-layer-background-color);
    outline: none;
    color: var(--light-font-color) !important;
    font-size: var(--main-font-size);
    font-family: var(--font-family);
    font-weight: var(--main-font-weight);
    letter-spacing: var(--main-letter-spacing);
}

.function-icon {
    filter: var(--icon-filter-dark);
    width: 30px;
}

#result-container {
    height: fit-content;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    justify-items: center;
}

.icon-weather-main {
    width: 160px;
}

.city-name {
    font-size: 40px;
    font-weight: 700;
    margin-bottom: 0;
}

.weather-name {
    font-size: 25px;
    font-weight: 700;
    margin-top: 0;
}

.forecast-title {
    font-size: 25px;
    font-weight: 700;
    color: var(--dark-font-color);
    justify-self: self-start;
    grid-column: 1/3;
}

.forecast-container {
    grid-column: 1/3;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 80px;
    margin-bottom: 40px;
}

.forecast-content {
    margin-bottom: 0;
    margin-top: 0;
    font-size: 16px;
}

.icon-weather-forecast {
    width: 100px;
}

.search-candidates-container {
    position: absolute;
    left: 50px;
    top: 70px;
    text-align: left;
    display: flex;
    flex-direction: column;
    gap: 0;
    background-color: var(--middle-layer-background-color);
    border-radius: 10px;
}

.search-candidates-container div {
    padding: 10px 20px;
}

.search-candidates-container div:hover {
    background-color: var(--upper-layer-background-color);
    border-radius: 10px;
}

.error-container {
    font-size: 30px;
    color: var(--dark-font-color);
    background-color: var(--upper-layer-background-color);
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,+BAA+B;IAC/B,+DAAqF;AACzF;;AAEA;IACI,uCAAuC;IACvC,wCAAwC;IACxC,uCAAuC;IACvC,0BAA0B;IAC1B,2BAA2B;IAC3B,2GAA2G;;IAE3G,6DAA6D;IAC7D,sBAAsB;IACtB,uBAAuB;IACvB,0BAA0B;IAC1B,wBAAwB;IACxB,yBAAyB;IACzB,4BAA4B;AAChC;;AAEA;IACI,qDAAqD;IACrD,+BAA+B;IAC/B,kBAAkB;IAClB,+BAA+B;IAC/B,kCAAkC;IAClC,sCAAsC;IACtC,4CAA4C;IAC5C,8BAA8B;AAClC;;AAEA;IACI,6BAA6B;IAC7B,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;IACvB,kBAAkB;AACtB;;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,SAAS;AACb;;AAEA;;;IAGI,sDAAsD;IACtD,YAAY;IACZ,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,SAAS;IACT,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,sDAAsD;IACtD,SAAS;AACb;;AAEA;IACI,YAAY;IACZ,SAAS;IACT,sDAAsD;IACtD,aAAa;IACb,yCAAyC;IACzC,gCAAgC;IAChC,+BAA+B;IAC/B,oCAAoC;IACpC,0CAA0C;AAC9C;;AAEA;IACI,+BAA+B;IAC/B,WAAW;AACf;;AAEA;IACI,mBAAmB;IACnB,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,qBAAqB;AACzB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,aAAa;AACjB;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,6BAA6B;IAC7B,wBAAwB;IACxB,gBAAgB;AACpB;;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,kCAAkC;IAClC,SAAS;IACT,mBAAmB;AACvB;;AAEA;IACI,gBAAgB;IAChB,aAAa;IACb,eAAe;AACnB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,SAAS;IACT,gBAAgB;IAChB,aAAa;IACb,sBAAsB;IACtB,MAAM;IACN,sDAAsD;IACtD,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,qDAAqD;IACrD,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,6BAA6B;IAC7B,qDAAqD;AACzD","sourcesContent":["@font-face {\n    font-family: 'radio-canada-big';\n    src: url('./resources/fonts/RadioCanadaBig-VariableFont_wght.ttf') format('truetype');\n}\n\n:root {\n    --lower-layer-background-color: #82D0D9;\n    --middle-layer-background-color: #36B1BF;\n    --upper-layer-background-color: #037F8C;\n    --dark-font-color: #F272A1;\n    --light-font-color: #F299B1;\n    --icon-filter-dark: invert(64%) sepia(37%) saturate(2182%) hue-rotate(298deg) brightness(99